import { useEffect, useState } from "react";

export const ExpandTxt = ({ abstract }) => {
  //   const [abstract, setdescripion] = useState();
  const [isLongTxt, setisLongTxt] = useState(false);
  useEffect(() => {}, []);

  const toggleIsTxtShown = () => {
    setisLongTxt(!isLongTxt);
  };

  return (
    <p className="pointer">
      {isLongTxt ? abstract : abstract.substring(0, 60) + "..."}
      <span onClick={toggleIsTxtShown}>
        {isLongTxt ? "[Read less]" : "[Read more]"}
      </span>
    </p>
  );
};
