import { useEffect, useState } from "react";
import { AiOutlineStar, AiFillStar } from "react-icons/ai";
import { moviesService } from "../services/moviesService";
import { ExpandTxt } from "./ExpandTxt";

export const MoviesPreview = ({ movie }) => {
  const [isFavorite, setisFavorite] = useState(false);

  useEffect(() => {}, []);

  const toggleFavorite = () => {
    setisFavorite(!isFavorite);
    movie.isFavorite = !isFavorite;
    moviesService.setIsFavorite(movie);
  };

  return (
    <div className="movies-preview flex align-center space-between">
      <div>
        <h4>{movie.title}</h4>
        <ExpandTxt abstract={movie.opening_crawl} />
      </div>
      <div className="pointer">
        {!movie.isFavorite && (
          <p onClick={toggleFavorite}>
            <AiOutlineStar />
          </p>
        )}
        {movie.isFavorite && <AiFillStar onClick={toggleFavorite} />}
      </div>
    </div>
  );
};
